import React, { useCallback, useEffect, useState } from "react";
import { Close, Configure } from "grommet-icons";
import Modal from "../../components/Modal";
import { Tab, Tabs, Box, Anchor, DataTable, Text, ResponsiveContext } from "grommet";
import { callCloud, crud } from "../../library/parse";
import CRUDForm from "../CRUDForm";

const Home = ({
  datasource,
  hideCRUDForm,
  suffix = "",
  objectClass,
  columns,
  fetchData,
}) => {
  const [activeModel, setActiveModel] = useState({});
  const [showFormModal, setShowFormModal] = useState(false);
  const [mode, setMode] = useState("read");
  const [stores, setStores] = useState(datasource || []);
  const [dtColumn, setDtColumn] = useState([]);
  const size = React.useContext(ResponsiveContext)
  const loadData = useCallback(async () => {
    return (
      fetchData &&
      setStores(await callCloud(fetchData.action, fetchData.params))
    );
  }, [fetchData]);

  useEffect(() => {
    if (datasource) {
      setStores(datasource);
    } else {
      loadData();
    }
  }, [loadData, datasource]);

  useEffect(() => {
    const newDtColumn = columns.map((e) => ({
      property: e.property,
      header: <Text> {e.headerText}</Text>,
      primary: e.primary,
      render: e.render,
    }));

    !hideCRUDForm &&
      newDtColumn.push({
        property: "objectId",
        header: (
          <Anchor
            onClick={() => {
              setActiveModel({});
              setMode("create");
              setShowFormModal(true);
            }}
          >
            Add {objectClass}
          </Anchor>
        ),
        render: (datum) => (
          <>
            <Configure
              onClick={() => {
                setActiveModel(datum);
                setMode("update");
                setShowFormModal(true);
              }}
              style={{ cursor: "pointer" }}
            />
            <Close
              onClick={() => {
                setActiveModel(datum);
                setMode("delete");
                setShowFormModal(true);
              }}
              style={{ cursor: "pointer" }}
            />
          </>
        ),
      });
    setDtColumn(newDtColumn);
  }, [hideCRUDForm, columns]);

  return (
    <>
    <Box>
      {stores.length} {objectClass}
      <DataTable columns={dtColumn} data={stores.map((e) => e.toJSON())} />

    </Box>
    {showFormModal && (
        <Modal 
          full={true}
          onClose={() => setShowFormModal(false)}
        >
          <Tabs>
            <Tab title="Information">
          <CRUDForm
            formConfig={{ config: columns }}
            mode={mode}
            model={activeModel}
            onClick={(mode, model) => {
              crud(objectClass, model, mode, suffix);
              setTimeout(() => {
                loadData();
                setShowFormModal(false);
              }, 1000);
            }}
          />
          </Tab>
          </Tabs>
        </Modal>
      )}
    </>
  );
};

export default Home;
